<template>
  <!-- eslint-disable vue/require-component-is -->
  <component v-bind="linkProps(to)">
    <slot/>
  </component>
</template>

<script>
    import {isExternal} from '@/utils/validate'

    export default {
        props: {
            to: {
                type: String,
                require: true
            }
        },
        methods: {
            linkProps(url) {
                if (isExternal(url)) {
                    return {
                        is: 'a',
                        href: url,
                        target: '_blank',
                        rel: 'noopener'
                    }
                }
                return {
                    is: 'router-link',
                    to: url
                }
            }
        }
    }
</script>

<style scoped>

</style>



